.icon {
  display: block;
  height: 24px;
  width: 24px;
  background-size: cover;
  background-position: center;

  @include onTablet {
    width: 32px;
    height: 32px;
  }

  &--phone {
    background-image: url(../images/icons/icon-phone.svg);
    position: relative;
    user-select: text;
    &::before {
      content: "+1 234 5555-55-55";
      font-family: "Inter", sans-serif;
      position: absolute;
      right: 40px;
      width: max-content;

      user-select: contain;

      color: $mainTextColor;
      text-align: right;
      font-size: 12px;
      font-weight: 700;
      letter-spacing: 2px;
      line-height: 32px;

      visibility: hidden;
      opacity: 0;

      transition: $transition-duration linear;
    }
    &:hover {
      &::before {
        visibility: visible;
        opacity: 1;
      }
    }
  }

  &--menu {
    background-image: url(../images/icons/icon-burger-menu.svg);

    &:hover {
      background-image: url(../images/icons/icon-burger-menu-hover.svg);
    }
  }

  &--close {
    background-image: url(../images/icons/icon-close.svg);
  }
}
